export default function MintBox() {
    const navigate = useNavigate();
    const {state} = useWeb3();
    const { NFTcontract} = state;
    const [NFTobj, setNFTobj] = useState(null);

    useEffect(() => {
        if(NFTcontract == null)return;

        const initTime = async () => {
            let lft = await nft.GetInfo(NFTcontract)
            setNFTobj(lft);
        }
        initTime();
    }, [NFTcontract]);

    const ToUrl = (url) => {
        if(!url){
            navigate('/')
        }else{
            navigate( url )
        }

    }
    const Confirm = async() =>{
    console.error("====Confirm=====")
        ToUrl(`/mint`)
    }
    return <MintBg>
        <Header />
        <Main>
            {
                NFTobj!=null &&<Contents>
                    <Lilft>
                        <Imgs src={demo1} alt=""/>
                    </Lilft>
                    <Lirht>
                        <Titles>{NFTobj.name}</Titles>
                        <ImgRht src={listli} alt="" />
                        <SubTit>Energy recovery speed</SubTit>
                        <Speed><span>2.2 </span> * N</Speed>
                        <SubTit>Number of complimentary iRon</SubTit>
                        <Speed><span>987,654,321,000.00</span></Speed>
                        <SubTit>Rarity</SubTit>
                        <StarV>
                            <Star value={4.5}/>
                        </StarV>
                        <RhtA onClick={()=>Confirm()} >Confirm</RhtA>
                    </Lirht>
                </Contents>
            }

        </Main>
    </MintBg>
}
