import { ethers, BigNumber } from 'ethers';
import abi from "../abi/MockERC20";

const GetInfo = async (contractInstance) =>{
    const collection = await contractInstance.allCollections(10001);
    return  collection
}
const drawBoxes = async (contractInstance,account,web3Instance) =>{

    const signer = web3Instance.getSigner()
    const { baseToken } = window.GeneralConfig;
    let baseContract = new ethers.Contract(baseToken, abi, web3Instance);
    // let balance = await baseContract.balanceOf(account);

    // console.log("========basetoken======",ethers.utils.formatEther(balance))
    // const gas = await contractInstance.connect(signer).estimateGas.drawBoxes(10001, 1)
    // console.log("=======gas=====",gas)

    let allowance = await baseContract.connect(signer).allowance(signer.getAddress(),contractInstance.address)

    const collection = await contractInstance.allCollections(10001);

    console.log("=====collection===",BigNumber.from(allowance.toString()) , BigNumber.from(collection.sellPrice.toString()))

    if(BigNumber.from(allowance.toString()).sub(BigNumber.from("1")) < BigNumber.from(collection.sellPrice.toString())){
        let rt =  await baseContract.connect(signer).approve(contractInstance.address,ethers.constants.MaxUint256);
        await rt.wait();
    }
    const result = await contractInstance.connect(signer).drawBoxes(10001, 1,
        // { value: ethers.utils.parseEther("10") }
        // {gasLimit:gas}
        )
    return  await result.wait();
    // }

}

export default {
    GetInfo,
    drawBoxes
};
