import React from 'react';
import styled from 'styled-components';

import maskbg from '../assets/images/modal/mask.png';
import mask60 from '../assets/images/modal/mask60.png';
import bg from '../assets/images/modal/bg.png';
import {useWeb3} from "../api/contracts";


const Mask = styled('div')`
     background: url(${maskbg}) no-repeat;
     background-size: 100% 100%;
     position: fixed;
     top: 0;
     left: 0;
     width: 100vw;
     height: 100vh;
     z-index: 99;
     opacity: ${ props => props.opacity || 1 };
`;

const Bg = styled('div')`
    background:url(${props => props.bg ? bg : mask60});
    background-size:100% 100% ;
     width: 100vw;
     height: 100vh;
     display: flex;
     justify-content: center;
     align-items: center;
`
const Middle = styled('div')`
    min-width: 2.2rem;
    text-align: center;
   
`

export default function Modal(props) {
    const { state } = useWeb3();
    const { showModalAct } = state;

    return<div>
        {
            showModalAct!=null && <Mask>
                <Bg bg={props.bg}>
                    <Middle>
                        {props.children}
                    </Middle>

                </Bg>
            </Mask>
        }

    </div>
}
