import React, { useState,useEffect} from "react";
import styled from 'styled-components';
import {useNavigate, useParams} from 'react-router-dom';

import Reback from "./components/reBack";

import MarketFoot from './components/market/marketfoot';
import Aircraft from "./components/market/aircraft";
import Equip from "./components/market/equip";
import IGas from "./components/market/igas";
import Diamond from "./components/market/diamond";
import IIS from "./components/market/iis";

import LftBg from './assets/images/LftBg.png';
import liBg from './assets/images/tag.png';
import liActive from  './assets/images/tagNOr.png';
import AllBg from './assets/images/marketBg.jpg';


const AppBg = styled.div`
  overflow: hidden;
  background:url(${AllBg}) no-repeat;
  background-size: 100% 100%;
  min-height:  100vh;
  
`;
const MainBrdr = styled.div`
  padding-top: 0.48rem ;
  box-sizing: border-box;
  width: 13.44rem;
  height: 100vh;
  margin: 0 auto;
`;

const TopMain = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: stretch;
  height: 100%;
`;

const Lft = styled.div`
   min-width: 20%;
  ul{
    background: url(${LftBg}) no-repeat;
    background-size: 100% 100%;

      height: calc(100vh - 1.45rem);
        
      padding: 0.6rem ;
      box-sizing: border-box;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
  }
  li{
    background: url(${liActive}) no-repeat;
    background-size: 100% 100%;
    width: 1.6rem;
    height: 0.56rem;
    line-height: 0.64rem;
    text-transform: capitalize;
    text-align: center;
    margin: 0.24rem 0;
    cursor: pointer;
    span{
      opacity: 0.6;
    }
     &.active{  
      background: url(${liBg}) no-repeat;
      background-size: 100% 100%;
     font-weight: 500;
      color: #0C0C0B;
       font-family: "Orbitron-Medium";
        span{
        opacity: 1;
        }
    }
  }
`;

const RhtTop = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  flex-grow: 1;
`;
const Rht = styled.div`
  width: 100%;
  padding-left: 0.36rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
`;


export default function Market() {
    const { id } = useParams();
    const navigate = useNavigate();

    const [active, setActive] = useState(1);




    useEffect(()=>{

        setActive(parseInt(id))
    },[id])

    const handleSelect = (index) =>{
        setActive(index)
        navigate(`/market/${index}`)


    }
    return <AppBg>
        <MainBrdr>
            <TopMain>
                <Lft>
                    <ul>
                        <li className={active ===1 ?'active':''} onClick={()=>handleSelect(1)}><span>Aircraft</span></li>
                        <li className={active ===2 ?'active':''} onClick={()=>handleSelect(2)}><span>equipment</span></li>
                        <li className={active ===3 ?'active':''} onClick={()=>handleSelect(3)}><span>iGas Energy</span></li>
                        <li className={active ===4 ?'active':''} onClick={()=>handleSelect(4)}><span>iGold</span></li>
                        <li className={active ===5 ?'active':''} onClick={()=>handleSelect(5)}><span>IIS</span></li>
                    </ul>
                    <Reback />
                </Lft>
                <Rht>
                    <RhtTop>
                        {
                            active ===1 && <Aircraft />
                        }
                        {
                            active ===2 && <Equip />
                        }
                        {
                            active ===3 && <IGas />
                        }
                        {
                            active ===4 && <Diamond />
                        }
                        {
                            active ===5 && <IIS />
                        }
                    </RhtTop>

                    <MarketFoot />
                </Rht>
            </TopMain>

        </MainBrdr>
        </AppBg>

}
