export default function Header() {
    const {  dispatch, state } = useWeb3();
    const { web3Instance,NFTcontract } = state;
    const navigate = useNavigate();
    const [list] = useState([{name:'Home'},{name:'Market'},{name:'WhiterPaper'},{name:'My iFighter'}]);
    const [account, setaccount] = useState('');
    const [showBg, setShowBg] = useState(false);
    // const [current, setCurrent] = useState(0);
    const [current] = useState(0);

    useEffect(() => {
        if(web3Instance == null )return
        const getChainId = async () => {
            // const chainId= await web3Instance.network.chainId;
            // chainChange(chainId);
        };
        getChainId()


        let selectedStorage = sessionStorage.getItem('account');
        if (selectedStorage) {
            dispatch({type: 'SET_ALLACCOUNT',payload:selectedStorage});
            setaccount(selectedStorage);
        }
    }, [web3Instance]);

    useEffect(() => {
        if(NFTcontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);


    if(window.ethereum){
        window.ethereum.on('accountsChanged', function (arr) {
            setaccount(arr[0]);
            window.location.reload();
        });
        window.ethereum.on('chainChanged', (chainId) => {
            chainChange(chainId);
            window.location.reload();
        });
    }else{
        console.log("===window.ethereum not found=====")
    }


    const chainChange = (chainId) =>{
        // const { chainName,nativeCurrency:{name,symbol,decimals},rpcUrls,blockExplorerUrls,onlineChainId } = window.GeneralConfig;
        // if (`0x${chainId.toString(16)}` !== onlineChainId.toString()) {
        //     // setshowError(true)
        //
        //     window.ethereum.request({
        //         method: 'wallet_addEthereumChain',
        //         params: [{
        //             chainId:onlineChainId,
        //             chainName,
        //             nativeCurrency: {
        //                 name,
        //                 symbol,
        //                 decimals
        //             },
        //             rpcUrls,
        //             blockExplorerUrls
        //         }]
        //     })
        //         .catch((error) => {
        //             console.log(error)
        //         })
        // } else {
        //     console.error("===Error===")
        // }
    }

    const handleSelect = (navIndex) => {
        // setCurNavIndex(navIndex);
        // let url;
        closeNavBg()
        switch (navIndex) {
            case 1:
                // navigate('/market/1');
                dispatch({type: 'SHOW_MODAL',payload: true});
                break;
            case 2:
                // url = '/market/1';
                window.open("https://wiki.ifighterinfinity.io/");
                navigate('/');
                break;
            case 3:
                // navigate('/myifighter/1');
                dispatch({type: 'SHOW_MODAL',payload: true});
                break;
            default:
                navigate('/');
                break;
        }
    }


    const AddresstoShow = (address) => {

        let frontStr = address.substring(0, 4);

        let afterStr = address.substring(address.length - 4, address.length);

        return `${frontStr}****${afterStr}`

    }

    const connect = async() => {
            const accoutlist = await Accounts.account();
            if(accoutlist.type === "success"){
                setaccount(accoutlist.data);
                dispatch({type: 'SET_ALLACCOUNT',payload:accoutlist.data});
                sessionStorage.setItem('account',accoutlist.data);
            } else {
                console.error("error")
            }
    }

    const showNavBg = () =>{
        document.body.parentNode.style.overflowY ="hidden";
        setShowBg(true)

    }
    const closeNavBg = () =>{
        document.body.parentNode.style.overflowY ="auto";
        setShowBg(false)

    }

    const handleclose = () =>{
        dispatch({type: 'SHOW_MODAL',payload:null});
    }
    return (
        <HeaderTop>
            <LoadingModal  />
            <Modal bg={true}>
                <div>
                    <img src={progress} alt=""/>
                    <Titles>Coming<br/> soon</Titles>
                    <BtnClose onClick={()=>handleclose()} src={close} alt="">Close</BtnClose>
                </div>
            </Modal>
            <div className="mainContent">
                <Logo>
                    <img src={logo} alt="" />
                </Logo>
                <RhtNav>
                    <Navlist home={true} list={list} handleSelect={handleSelect} />
                </RhtNav>
                <NaviRht>
                    {
                        !account && <BtnBuy onClick={()=>connect()} />
                    }
                    {
                        account && <WalletBg><img src={Green} alt=""/>{AddresstoShow(account)}</WalletBg>
                     }

                    {/*<BtnStart>start</BtnStart>*/}
                </NaviRht>
                {
                    !showBg &&<RhtIcon onClick={()=>showNavBg()}>
                        <img src={Navi} alt=""/>
                    </RhtIcon>
                }
                {
                    showBg &&<RhtIcon onClick={()=>closeNavBg()}>
                        <img src={close} alt=""/>
                    </RhtIcon>
                }
            </div>
            {
                showBg &&  <NavBg>
                    <Toplist>
                        <Ul>
                            {
                                list.map((item,index)=>(
                                    <li className={current === index ? "active":0} key={`navlist_${index}`} onClick={()=>handleSelect(index)}>
                                        {
                                            current === index &&    <img src={arrowrht} alt=""/>
                                        }
                                        <span>{item.name}</span>
                                        {
                                            current === index &&    <img src={arrowlft} alt=""/>
                                        }
                                    </li>
                                ))
                            }

                        </Ul>
                        <Imgbg>
                            <img src={Plane} alt=""/>
                        </Imgbg>

                    </Toplist>

                    <Links>
                        <a href="https://t.me/iFighterInfinity" target="_blank" rel="noopener noreferrer"><img src={tg} alt="" /></a>
                        <a href="https://twitter.com/iFighterGame" target="_blank" rel="noopener noreferrer"><img src={twitter} alt="" /></a>
                        <a href="https://www.youtube.com/channel/UCKmChoqJCpqTi9J0wh9U5-Q" target="_blank" rel="noopener noreferrer"><img src={youtube} alt="" /></a>
                        <a href="https://www.facebook.com/IFighter-Infinity-110157721501891/?ref=pages_you_manage" target="_blank" rel="noopener noreferrer"><img src={facebook} alt="" /></a>
                        <a href="https://discord.gg/2xHzE62SQF" target="_blank" rel="noopener noreferrer"><img src={discord} alt="" /></a>
                        <a href="https://medium.com/@ifighterinfinity" target="_blank" rel="noopener noreferrer"><img src={medium} alt="" /></a>
                    </Links>
                </NavBg>
            }

        </HeaderTop>
    )
}
